<template>
    <div class="myAllSearch">
         <van-nav-bar
                title="搜索记录"
                left-text="返回"
                @click-left="back"
                left-arrow
         />
            <!-- 搜索宠物展示 -->
        <div class="popularMenu">
          <!-- <div class="popularMenu_title">猫科</div> -->
          <!-- <div class="popularMenu_title">
            <div class="popularMenu_title_item">{{ShowStyleName}}</div>
          </div> -->

          <div class="popularMenu_liatItem">
            <div class="clearfix">
              <div class="popularMenu_imgData fl" v-for="(item,index) in showAllData" :key="index" @click="goDetail('mySearchData',item.name,index)">
                <div class="popularMenu_img">
                  <img v-lazy="item.coverURL" />
                </div>
                <div>
                  <div class="popularMenu_imgData_title">
                      <div class="popM_imgD_tItem on-title">{{item.name}}</div>
                       <!-- <div class="pets_link" @click.stop="delSearchData(index,item)">
                           <van-icon
                                name="delete"
                                color="#1989fa"
                                class="item_watch_icon"
                                size="20"
                                           
                            />
                      </div> -->
                  </div>
                  <div class="on-title">{{item.desc}}</div>
                </div>
              </div>
            </div>
          </div>
        </div>
    </div>
</template>

<script>
import '../assets/css/MyAllSearch.less'
import {createNamespacedHelpers} from 'vuex'

const {mapState,mapMutations} = createNamespacedHelpers('homeModules')
    export default {
        data(){
            return {
                showAllData: null
            }
        },
         computed: {
           ...mapState(["mySearchData"])
        },
        created(){
             //获取本地存储数据
          let localSData=localStorage.getItem('pestData');
          localSData=JSON.parse(localSData);
          if(localSData!=null){
             this.showAllData=localSData['mySearchData'];
          }else{
            this.showAllData=this.mySearchData;
          }
           
        },
        methods: {
            ...mapMutations(['removeMySearchData']),
            back(){
                this.$router.go(-1)
            },
            goDetail(styleName,name,type){
                 this.$router.push({ name: "Detail", query: { name, type,styleName } });
            },
            delSearchData(index,item){
                this.removeMySearchData(index);
            }
        }
    }
</script>